:root {
  --gap: 4rem;
  --gap-xsm: calc(var(--gap)/4);
  --gap-sm: calc(var(--gap)/2);
  --gap-lg: calc(var(--gap)*2);
}

$breakpoint-tablet: 768px;

@font-face {
    font-family: 'Bluu';
    src: url('../fonts/bluunext-bold-webfont.woff2') format('woff2'),
         url('../fonts/bluunext-bold-webfont.woff') format('woff'),
         url('../fonts/bluunext-bold.ttf') format('truetype'),
         url('../fonts/bluunext-bold-webfont.svg#bluu_nextbold') format('svg');
    font-weight: normal;
    font-style: normal;

}

html {
  scroll-behavior: smooth;
  scroll-padding-top: var(--gap);
}

body {
  font-family: 'Syne', sans-serif;
  color: #181818;
  background-color: #FDFAF2;

  //background: linear-gradient(45deg, rgba(253,250,242,1) 51%, rgba(240,232,207,1) 100%);
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden; //Need to rework grid so this isn't needed
}

p {
  font-size: 1.1rem;
}

h3 {
  font-weight: 600;
  line-height: 1.75;
}

.muted {
  color: #969696;
}

.grid {
  display: grid;
}

.grid-main {
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: var(--gap) 1fr;
  padding: 0 calc(var(--gap)/2);

  @media (min-width: $breakpoint-tablet) {
    grid-template-columns: var(--gap) repeat(8, 1fr) var(--gap);
    grid-template-rows: var(--gap) 1fr;
    grid-column-gap: var(--gap);
    grid-row-gap: var(--gap);
    max-width: calc(22*var(--gap));
    margin: 0 auto;
  }
}

.grid-cases {

  @media (min-width: $breakpoint-tablet) {
    grid-template-columns: var(--gap) repeat(8, 1fr) var(--gap);
    grid-template-rows: 1fr var(--gap);
    grid-column-gap: var(--gap);
    grid-row-gap: var(--gap);
    max-width: calc(22*var(--gap));
    margin: 0 auto;
  }
}

.logo {
  grid-column: 1 / 5;
  grid-row: 1 / 3;

  @media (min-width: $breakpoint-tablet) {
    grid-column: 2 / 5;
  }
}

.nav-main {
  grid-column: 3 / -1;
  grid-row: 1 / 2;
  display: grid;
  align-items: center;

  @media (min-width: $breakpoint-tablet) {
    justify-content: end;
    grid-column: 6 / -1;
  }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;

    @media (max-width: $breakpoint-tablet) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: 1rem;
    }
  }

  .btn {

    @media (max-width: $breakpoint-tablet) {
      margin-top: 0;
    }
  }
}

li {
  display: inline;
}

.nav-main a {
  text-transform: uppercase;
  font-weight: 600;
  color: #181818;
  text-decoration: none;

  font-size: .8rem;

  @media (min-width: $breakpoint-tablet) {
    font-size: .9rem;
    margin-left: 1rem;
  }
}

h1 {
  font-size: 2rem;
  margin: 0;
  font-weight: 600;

  @media (min-width: $breakpoint-tablet) {
    font-size: 2.5rem;
  }
}

.tagline {
  font-size: 1.75rem;
  color: #969696;

  @media (min-width: $breakpoint-tablet) {
    font-size: 2rem;
  }

  .wrapper-home & {
      margin-bottom: 2rem;
  }
}

.intro {
  grid-column: 1 / 6;

  @media (min-width: $breakpoint-tablet) {
    grid-column: 6 / 9;
    grid-row: 2 / 3;
  }
}

h2 {
  font-weight: 400;
  color: #5A373B;
  text-transform: uppercase;
}

p {
  line-height: 1.75;
}

strong {
  font-weight: 600;
}

img {
  max-width: 100%;
}

.mt-0 {
  margin-top: 0;
}

.mb-0 {
  margin-bottom: 0;
}

.mb-2 {
  margin-bottom: 2rem !important;
}

.mb-4 {
  margin-bottom: 4rem !important;
}

.mb-6 {
  margin-bottom: 6rem !important;
}

.mt-2 {
  margin-top: 2rem;
}

.mt-6 {
  margin-top: 6rem;
}

.job-entry {
  margin-top: 3rem;
}

.job-company {
  font-weight: 600;
}

.job-timespan {
  color: #707070;
}

.cases {

  @media (max-width: $breakpoint-tablet) {
    margin: 0 auto;
    width: calc(100vw - (var(--gap)));
  }

  @media (min-width: $breakpoint-tablet) {
    display: grid;
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    margin-top: -16rem;
  }
}

.case {
  margin-bottom: var(--gap);

  @media (min-width: $breakpoint-tablet) {
    display: grid;
    grid-template-columns: var(--gap) repeat(8, 1fr) var(--gap);
    grid-column-gap: var(--gap);
    height: fit-content;
    align-items: end;
    margin-bottom: calc(2*var(--gap));

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.case-cover {
  grid-column: 2 / 6;
  border-radius: 1rem;
  //overflow: hidden;
  position: relative;
}

.case-description {
  grid-column: 6 / 9;
}

.case-logo {
  position: absolute;
  height: 73px;
  left: 2rem;
  top: 2rem;
}


.boom-headshot {
  margin-bottom: calc(var(--gap)/2);

}

.boom-headshot img {
  max-width: 100px;
  border-radius: 0 0 100px 100px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;

  @media (min-width: $breakpoint-tablet) {
    max-width: 200px;
  }
}

.desc {
  display: grid;
  grid-template-columns: 100px auto;
  align-items: baseline;
  border-bottom: 1px solid #D9D9D9;
}

.no-border {
  border: 0;
}

.desc-title {
  color: #969696;
  font-weight: 600;
  text-transform: uppercase;
}

.btn {
  background-color: #5A373B;
  color: #fff;
  text-decoration: none;
  padding: .5rem 1rem;
  padding-right: 3rem;
  border-radius: 2rem;
  text-transform: uppercase;
  font-weight: 600;
  display: inline-block;
  margin-top: 1rem;
  position: relative;
  transition: padding-right .2s ease-out, background-color .2s ease-out;
}

.btn:hover {
  padding-right: 3.25rem;
  background-color: #181818;
}

.btn:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 20px;
  right: 1rem;
  top: 0;
  background: url('../img/arrow.svg') center center no-repeat;
}

a.btn-linkedin {
  background-color: rgb(10, 102, 194);
  color: #fff;

  &:after {
    background: url('../img/linkedin-logo.svg') center center no-repeat;
  }

  &:hover {
    padding-right: 3rem;
    background-color: rgb(11, 94, 134);
  }
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #D9D9D9;
  padding: 0;
  margin: calc(var(--gap)/2) 0;
}

.cover-shadow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  filter: blur(15px);
}

.cover-sharp {
  position: relative;
}

.soon {
  position: absolute;
  right: 2rem;
  bottom: 2rem;
  font-size: 2rem;
  font-weight: 600;
  color: #fff;
}

.header {
  grid-column: 1 / -1;
  justify-content: space-between;
}

.logo-small {

  h1 {
    font-size: 1.15rem;
  }

  .tagline {
    font-size: 1rem;
  }
}

.desc-long {
  grid-column: 1 / 5;
}

.desc-more {
  grid-column: 6 / 10;
}

.desc-more-2 {
  grid-column: 5 / 11;

  img {
    margin-bottom: calc(-2*var(--gap));
  }
}



.case-screen-1 {
    grid-column: 6 / 9;

    img {
      //box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    }
}

.grid-ds {
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(3, auto);
  grid-column-gap: 16px;
  grid-row-gap: var(--gap);
  width: calc(100vw - (var(--gap)));
  max-width: calc(22*var(--gap));
  margin: 0 auto;
}

.ds-bg {
  background-color: rgba(0, 0, 0, .05);
  grid-column: 1 / -1;
  grid-row: 2 / -1;
  margin: 8rem -999rem 0 -999rem;
}

.ds-gradient {
  background: linear-gradient(to top, #FDFAF2 24rem, transparent);
  grid-column: 1 / -1;
  grid-row: 2 / -1;
  margin: 0 -1rem;
}

.ds-1 {
  grid-column: 1 / -1;
  grid-row: 1 / 2;
}

.ds-title {
  grid-column: 6 / 8;
  grid-row: 1 / 2;
  padding-top: 5rem;
}


.ds-screenshot {
  grid-row: 2 / 3;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
  border-radius: 8px;

  &:nth-child(even) {
    margin-top: 2rem;
  }
}

.ds-screenshot-1 { grid-column: 1 / 3; }
.ds-screenshot-2 { grid-column: 3 / 5; }
.ds-screenshot-3 { grid-column: 5 / 7; }
.ds-screenshot-4 { grid-column: 7 / 9; }
.ds-screenshot-5 { grid-column: 9 / 11; }
.ds-screenshot-6 { grid-column: 11 / -1; }

.ds-devices {
    grid-row: 3 / -1;
    grid-column: 2 / 11;
    margin-top: -12rem;
    margin-left: 1rem; //visually centered
}

.ds-subtitle {
  color: #E27865;
  font-size: 1.5rem;
  padding-top: 1rem;
  display: block;
  font-weight: 600;
}

.hot-probeersel {
  grid-column: 1 / -1;
  //grid-row: 1 / 2;
}

.hot-probeersel-2 {
  grid-column: 1 / -1;
  grid-row: 2 / -1;
}

.hot-jobs {
  grid-column: 1 / -1;
  //grid-row: 2 / -1;
}

.wrapper2 {
  width: 100vw;
  background-color: hsl(46, 71, 94);
  padding-bottom: var(--gap);
  margin-bottom: calc(var(--gap)*3);
  border-bottom: 1px solid hsl(46, 71, 80);
}

.wrapper-home {
  width: 100vw;
  background-color: hsl(46, 71, 94);
  padding-bottom: var(--gap);
  margin-bottom: var(--gap);
  border-bottom: 1px solid hsl(46, 71, 80);

  @media (min-width: $breakpoint-tablet) {
    padding-bottom: calc(var(--gap)*3);
    margin-bottom: calc(var(--gap)*3);
  }
}

.wrapper-footer {
  width: 100vw;
  background-color: hsl(46, 71, 94);
  padding-bottom: var(--gap);
  border-top: 1px solid hsl(46, 71, 80);

  @media (min-width: $breakpoint-tablet) {
    margin-top: var(--gap);
  }

  .cases {
    margin-top: -4rem;
  }

  .case {
    margin-bottom: 0;
  }
}

.shadow-lg {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
}

.br-lg {
  border-radius: 8px;
}

.page-title {
  margin-bottom: 1.5rem;

}

.hot-extra-text {
  margin: 0 auto;
  max-width: 30rem;
  margin-bottom: calc(var(--gap)*2);
}

.hot-extra-text2 {
  grid-column: 5 / 9;
}

.hot-probeersel-wrapper {

  grid-column: 1 / -1;
}

h1,
h2 {
  font-family: 'Bluu';
}

.grid-hot {
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: repeat(11, 1fr);
  max-width: calc(22*var(--gap));
  margin: 0 auto;
  width: calc(100vw - (var(--gap)));

  & > * {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    border-radius: 8px;
  }

  & > *:nth-child(1) {
    grid-column: 1 / -1;
    grid-row: 1 / 4;
  }

  & > *:nth-child(2) {
    grid-column: 10 / 15;
    grid-row: 3 / 6;
    align-self: end;
  }

  & > *:nth-child(3) {
    display: none;
  }

  & > *:nth-child(4) {
    display: none;
  }

  & > *:nth-child(5) {
    grid-column: 2 / 8;
    grid-row: 3 / 10;
  }

  & > *:nth-child(6) {
    grid-column: 3 / 17;
    grid-row: 6 / 11;
  }

  & > *:nth-child(7) {
    grid-column: 2 / 7;
    grid-row: 10 / -1;
  }

  & > *:nth-child(8) {
    grid-column: 5 / 16;
    grid-row: 8 / -1;
  }

  & > *:nth-child(9) {
    display: none;
  }

  & > *:nth-child(10) {
    grid-column: 1 / 13;
    grid-row: 10 / -1;
  }

  & > *:nth-child(11) {
    display: none;
  }

  @media (min-width: $breakpoint-tablet) {
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(5, auto);
    grid-column-gap: calc(var(--gap)/1.5);
    grid-row-gap: calc(var(--gap)/1.5);


    & > * {
      box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
      border-radius: 8px;
    }

    & > *:nth-child(1) {
      grid-column: 1 / 9;
      grid-row: 1 / 2;
    }

    & > *:nth-child(2) {
      grid-column: 9 / 11;
      grid-row: 1 / 2;
      align-self: end;
    }

    & > *:nth-child(3) {
      grid-column: 11 / -1;
      grid-row: 1 / 2;
      display: block;
    }

    & > *:nth-child(4) {
      grid-column: 2 / 4;
      grid-row: 2 / 3;
      align-self: end;
      display: block;
    }

    & > *:nth-child(5) {
      grid-column: 4 / 6;
      grid-row: 2 / 3;
    }

    & > *:nth-child(6) {
      grid-column: 6 / 12;
      grid-row: 2 / 3;
    }

    & > *:nth-child(7) {
      grid-column: 2 / 4;
      grid-row: 3 / 4;
    }

    & > *:nth-child(8) {
      grid-column: 4 / 10;
      grid-row: 3 / 4;
    }

    & > *:nth-child(9) {
      grid-column: 10 / -1;
      grid-row: 3 / 4;
      align-self: end;
      display: block;
    }

    & > *:nth-child(10) {
      grid-column: 3 / 8;
      grid-row: 4 / 5;
    }

    & > *:nth-child(11) {
      grid-column: 8 / 12;
      grid-row: 4 / 5;
      display: block;
    }
  }
}

.mt-gap-2 {
  margin-top: calc(var(--gap)*2)
}

.mt-gap-3 {
  margin-top: calc(var(--gap)*3)
}

.grid-helan {
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(12, auto);
  grid-column-gap: calc(var(--gap)/1.5);
  grid-row-gap: calc(var(--gap)/1.5);
  max-width: calc(22*var(--gap));
  margin: 0 auto;

  & > * {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    border-radius: 8px;
  }

  & > *:nth-child(1) {
    grid-column: 1 / 5;
    grid-row: 2 / 3;
  }

  & > *:nth-child(2) {
    grid-column: 2 / 6;
    grid-row: 3 / 4;
    border: 8px solid white;
  }

  & > *:nth-child(3) {
    grid-column: 1 / 5;
    grid-row: 4 / 6;
    border: 8px solid white;
  }

  & > *:nth-child(4) {
    grid-column: 2 / 6;
    grid-row: 5 / 7;
    align-self: end;
    border-radius: 32px;
  }

  & > *:nth-child(5) {
    grid-column: 2 / 6;
    grid-row: 8 / 11;
    border: 8px solid white;
    z-index: -1;
    margin-top: -2rem;
  }

  & > *:nth-child(6) {
    grid-column: 2 / 7;
    grid-row: 5 / 8;

  }

  & > *:nth-child(7) {
    grid-column: 2 / 6;
    grid-row: 1 / 2;
    box-shadow: none;
  }

  @media (min-width: $breakpoint-tablet) {
    width: calc(100vw - (var(--gap)));
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(6, 1fr);

    & > *:nth-child(1) {
      grid-column: 1 / 5;
      grid-row: 1 / 3;
    }

    & > *:nth-child(2) {
      grid-column: 9 / 13;
      grid-row: 1 / 3;
      align-self: end;
      margin-bottom: 2rem;
      border: 8px solid white;
    }

    & > *:nth-child(3) {
      grid-column: 4 / 10;
      grid-row: 3 / 5;
      margin-top: -6rem;
      border: 8px solid white;
    }

    & > *:nth-child(4) {
      grid-column: 2 / 5;
      grid-row: 3 / 4;
      align-self: end;
      border-radius: 32px;
    }

    & > *:nth-child(5) {
      grid-column: 9 / 13;
      grid-row: 4 / 8;
      border: 8px solid white;
      z-index: -1;
      margin-top: -5rem;
    }

    & > *:nth-child(6) {
      grid-column: 1 / 6;
      grid-row: 4 / 8;

    }

    & > *:nth-child(7) {
      grid-column: 6 / 8;
      grid-row: 1 / 3;
      box-shadow: none;
      margin-top: 5rem;

      .ds-subtitle {

      }
    }
  }
}

.quote {
  text-transform: none;
  font-size: 2rem;
}

.grid-tb {
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(5, auto);
  grid-column-gap: calc(var(--gap)/1.5);
  grid-row-gap: calc(var(--gap)/1.5);
  max-width: calc(22*var(--gap));
  margin: 0 auto;

  & > * {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    border-radius: 8px;
  }

  & > *:nth-child(1) {
    grid-column: 1 / -1;
    grid-row: 4 / 5;
    box-shadow: none;
  }

  & > *:nth-child(2) {
    grid-column: 2 / 8;
    grid-row: 1 / 5;
  }

  & > *:nth-child(3) {
    grid-column: 9 / 12;
    grid-row: 4 / 6;
    margin-top: calc(var(--gap)*1.5);
  }

  & > *:nth-child(4) {
    grid-column: 2 / 7;
    grid-row: 4 / 5;
    align-self: end;
  }

  & > *:nth-child(5) {
    grid-column: 4 / 6;
    grid-row: 2 / 3;
  }

  & > *:nth-child(6) {
    grid-column: 6 / 12;
    grid-row: 2 / 3;
  }

  & > *:nth-child(7) {
    grid-column: 2 / 4;
    grid-row: 3 / 4;
  }

  & > *:nth-child(8) {
    grid-column: 4 / 10;
    grid-row: 3 / 4;
  }

  & > *:nth-child(9) {
    grid-column: 10 / -1;
    grid-row: 3 / 4;
    align-self: end;
  }

  & > *:nth-child(10) {
    grid-column: 3 / 8;
    grid-row: 4 / 5;
  }

  & > *:nth-child(11) {
    grid-column: 8 / 12;
    grid-row: 4 / 5;
  }
}


.container-tb {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: repeat(11, 1fr);
  //grid-column-gap: calc(var(--gap)/1.5);//
  //grid-row-gap: calc(var(--gap)/1.5);//

  margin: 0 auto;
  grid-auto-flow: row;

  width: calc(100vw - (var(--gap));

  @media (min-width: $breakpoint-tablet) {
    grid-template-rows: repeat(16, 1fr);
    height: calc(100vw - (var(--gap));

    max-width: calc(22 * var(--gap) );
    max-height: calc(22 * var(--gap) );
  }

  img {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    border-radius: 8px;
  }
}

.container-tb-2 {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: repeat(11, 1fr);
  width: calc(100vw - (var(--gap)));
  max-width: calc(22 * var(--gap) );
  margin: 0 auto;

  @media (max-width: $breakpoint-tablet) {
    margin-bottom: 10rem !important;
  }

  img {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    border-radius: 8px;
  }
}

.filler {
  grid-area: 4 / 1 / 12 / 17;

  @media (min-width: $breakpoint-tablet) {
    grid-area: 6 / 1 / 12 / 17;
  }

  img {
    box-shadow: none;
  }
}

.img-one {
  grid-area: 1 / 2 / 10 / 14;

  @media (min-width: $breakpoint-tablet) {
    grid-area: 1 / 2 / 10 / 10;
  }
}

.img-two {
  grid-area: 6 / 11 / 11 / 16;

  @media (min-width: $breakpoint-tablet) {
    grid-area: 3 / 12 / 11 / 16;
  }
}

.img-three {
  grid-area: 6 / 1 / 17 / 10;

  @media (max-width: $breakpoint-tablet) {
    margin-top: -2rem;
    margin-left: -1rem;
  }

  @media (min-width: $breakpoint-tablet) {
    grid-area: 10 / 4 / 17 / 11;
  }
}

.img-six {
  grid-area: 1 / 1 / 6 / 12;

  @media (min-width: $breakpoint-tablet) {
    grid-area: 1 / 1 / 17 / 8;
  }
}

.img-seven {
  grid-area: 6 / 2 / 17 / 17;

  @media (min-width: $breakpoint-tablet) {
    grid-area: 1 / 10 / 17 / 17;
  }
}

.img-eight {
  grid-area: 4 / 9 / 14 / 16;

  @media (min-width: $breakpoint-tablet) {
    grid-area: 3 / 7 / 17 / 11;
  }
}

.logo-small a {
  color: #181818;
  text-decoration: none;
}

.header-small {

  @media (min-width: $breakpoint-tablet) {
    display: flex;
    align-items: end;
  }

  .logo-small {
    justify-content: center;
    display: flex;
    flex-direction: column;

    @media (max-width: $breakpoint-tablet) {
      align-items: center;
      padding-top: var(--gap-xsm);
    }
  }
}

.wrapper-cv {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, auto);
  margin: var(--gap) auto;

  width: calc(100vw - (var(--gap));
  max-width: calc(16*var(--gap));
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
  border-radius: 8px;

  grid-column-gap: calc(var(--gap)/1.5);
  grid-row-gap: calc(var(--gap)/1.5);

  .boom-headshot {
    grid-area: 1 / 1 / 1 / 3;
    margin-bottom: 0;
    text-align: right;

    img {
      box-shadow: none;
      max-width: 125px;
    }
  }

  .name {
    grid-area: 1 / 3 / 1 / 7;
    align-self: end;
  }

  .data {
    grid-area: 2 / 3 / 2 / 8;
    display: grid;
    grid-template-columns: 1fr 1fr;


  }

  .experience-title {
    grid-area: 4 / 1 / 4 / 3;
    text-align: right;

  }

  .experience {
    grid-area: 4 / 3 / 4 / 8;
  }

  .projects-title {
    grid-area: 6 / 1 / 6 / 3;
    text-align: right;

  }

  .projects {
    grid-area: 6 / 3 / 6 / 8;
  }

  p,
  h3,
  dt,
  dd {
    line-height: 1.5;
  }

  .divider-one {
    grid-area: 3 / 1 / 3 / 9;
  }

  .divider-two {
    grid-area: 5 / 1 / 5 / 9;
  }

  dt {
    text-transform: uppercase;
    color: #969696;
    font-size: 14px;
  }

  a {
    color: #5A373B;
  }
}

// REFACTOR ///////////////////////////

.row {
  margin: 0 auto;
  max-width: 30rem;
  padding: 0 calc(var(--gap)/2);

  //margin-bottom: calc(var(--gap)*2); // utility class

  @media (min-width: $breakpoint-tablet) {

  }
}

.mb-sm-1 {
  margin-bottom: var(--gap);
}

.mb-md-2 {

  @media (min-width: $breakpoint-tablet) {
    margin-bottom: calc(var(--gap)*2);
  }
}

.wrapper-header-case {
  //width: 100vw;
  background-color: hsl(46, 71, 94);
  padding: 0 calc(var(--gap)/2);
  margin-bottom: var(--gap-lg);
  border-bottom: 1px solid hsl(46, 71, 80);

  @media (min-width: $breakpoint-tablet) {
    margin-bottom: calc(var(--gap)*3);
    padding-bottom: var(--gap);
  }
}

.grid-header-case {
  grid-template-columns: 1fr;
  grid-template-rows: var(--gap) 1fr auto;
  grid-column-gap: var(--gap);
  grid-row-gap: var(--gap);
  max-width: calc(22*var(--gap));
  margin: 0 auto;

  @media (min-width: $breakpoint-tablet) {
    grid-template-columns: var(--gap) repeat(8, 1fr) var(--gap);
    grid-template-rows: var(--gap) 1fr;
  }
}

.case-intro {

  @media (min-width: $breakpoint-tablet) {
    grid-column: 1 / 5;
  }
}

.case-snapshot {
  grid-column: 1 / -1;
  grid-row: 3 / 3;

  @media (min-width: $breakpoint-tablet) {
    grid-column: 5 / 11;
    grid-row: 2 / 2;
    margin-bottom: calc(-2*var(--gap));
  }

  @media (max-width: $breakpoint-tablet) {
    margin: calc(var(--gap)/(-2));
  }
}

.lead {
  font-size: 1.4rem;
  line-height: 1.5;

  @media (min-width: $breakpoint-tablet) {
    font-size: 1.5rem;
    line-height: 1.6;
  }
}

.site-footer {
  border-top: 1px solid #f0dfa8;

  .container {
    width: calc(100vw - (var(--gap)));
    margin: 0 auto;

    @media (max-width: $breakpoint-tablet) {
      max-width: calc(22*var(--gap));
    }
  }

  ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
  }

  .btn {
    margin-top: 0;
  }
}

.mobile-hidden {

  @media (max-width: $breakpoint-tablet) {
      display: none;
  }
}
